@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#scores {
  display: table;
  width: 100%;
}
.scoreLine {
  display: table-row;
  height: 1.5em;
  font-size: 0.8em;
}


.scoreName {
  width: 50%;
  display: table-cell	;
  text-align: right;
  padding-right: 0.5em;
  font-weight: bold;

}

.scoreText {
  width: 50%;
  display: table-cell	;
  display: table-cell	;
  text-align: left;
  padding-left: 0.5em;



}



body {
  font-family: "Montserrat";
  text-align: center;
  background-color: #00BFFF;
}

#bugboxcontainer {
  display: flex;
  flex-direction: row;
  position: relative;
  width: 100%;
  height: 600px;

}
#bugbox {
  width: 95%;
  height: 0;
  
  padding-bottom: 100%;
  position: relative;
  background-color: #6495ED;
  border: 1px #6495ED solid;
  box-sizing: border-box;

}

#naughtybar, #rewardbar {
  padding: 0;
  height: 0;
  position: relative;
  width: 2.5%;

  padding-bottom: 100%;

}


#instructions div p {
  margin-top: 0.4em;
  margin-bottom: 0.4em;

}

#instructions div:last-of-type p{
  margin-top: 1em;
  font-weight: 900;

}

.scoreLine {
  display: flex;
  height: 1.5em;
  font-size: 0.8em;
}

.scoreLineF {
  font-size: 1em;
}

.learning p {
  font-size: 0.9em;

}
#initialEndScore {
  margin: 0;
  margin-bottom: 0.5em;
  font-weight: 800;
}
.scoreSectionHead {
  margin: 0;
  text-transform: uppercase;
  font-size: 0.8em;
  font-weight: 900;
}
.finalEndScore {
  margin: 0;
  margin-top: 0.5em;
  font-weight: 900;

}
.scoreBug {
  height: 100%;
  width: 2em;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}


.scoreEndS {
  margin: auto;
}

#volumecontrol {
  position: absolute;
  top: 0;
  right: 0;
  color: white;
  z-index: 10;
}



.startButton {
  width: 60%;
  font-size: 2em;
  font-weight: 700;
  border: 5px black solid;
  background-color: #4FFFB0;
  display: flex;
  height: 1.5em;
  margin-top: 0.2em;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.endButton {
  font-size: 1.5em;
}

.startButton:hover {
  background-color: white;

}


.App {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.rotate, .fire {
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
  background-repeat: no-repeat;
  background-size: 90%;
  background-position: center;
}

h1 {
  font-weight: 900;
  font-size: 1em;
  color: white;
  z-index: 10;
}


.title {
  color: #4FFFB0;
  font-size: 3em;
  width: 90%;
  margin-top: 0.1em;
  margin-bottom: 0.1em;
}

.spacer {
  height: 0em;
  margin: 0;
}

.intro {
  margin-top: 0.25em;
  margin-bottom: 0.25em;
  font-size: 0.8em;
}
.label {
  font-size: 0.7em;
  font-weight: 600;
  text-transform: uppercase;
  margin: 0;
  margin-top: 0.2em;
}
#logo {
  height: 4em;
}
#scorebar {
  position: absolute;
  top: 4em;
  left: 2.5%;
  font-weight: 700;
  width: 98%;
  display: flex;
  flex-direction: row;
  text-align: center;
  align-items: center;
  justify-content: space-between;
  color: white;
  padding-left: 1%;
  padding-right: 1%;
  z-index: 100;

}

.container {
  width: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: flex-start;
}

#buttons {
  position: relative;
  width: 100%;
}

#buttons button {
  border: 1px black solid;

}

#rbuttons {
  position: absolute;
  left: 0;
  display: flex;
  flex-direction: row;
}

#fbuttons {
  position: absolute;
  right: 0;
  display: flex;
  flex-direction: row;
}

.gbutton {
  height: 25vw;
  width: 20vw;
}
.gbutton button {
  width: 100%;
  height: 80%;
}


.letter {
  display: none;
}

.fire img {
  height: 100%;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
}



#matterBox {
  width: 100%;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;

}

#matterBox canvas {
  width: 100%;
}

#allowedornot {
  top: 105vw;
}

@media screen and (min-width: 500px) {
  .container {
    width: 500px;
  }

  #allowedornot {
    top: 525px;
  }

  .gbutton {
    height: 100px;
    width: 80px;
  }

  .letter {
    display: block;
  }
  
}

